.BuildControls {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    margin: auto;
    padding: 10px 0;
}

.OrderButton {
    background-color: #ebe700;
    outline: none;
    cursor: pointer;
    border: 1px solid #bbcf00;
    border-radius: 50px;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px #966909;
            box-shadow: 2px 2px 2px #966909;
}

.OrderButton:hover,
.OrderButton:active {
    background-color: #65da22;
    border: 1px solid #008a12;
    color: #fff4b5;
}

.OrderButton:disabled {
    background-color: #c7c6c6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



